import { categoryList } from './cards';
// eslint-disable-next-line import/no-cycle
import renderTable from './statistic';
import { renderCategories, cardsOfCategory } from './renderCategory';
import { clearGame, renderGame } from './game';

const menuWrap = document.querySelector('.menu');
const menuBtn = document.querySelector('.menu__btn');
const title = document.querySelector('.title');

function activHref(href) {
  menuWrap.querySelectorAll('li').forEach((a) => a.classList.remove('active'));
  href.classList.add('active');
}

export const closeMenu = (btn) => {
  btn.classList.remove('is-active');
  menuWrap.classList.remove('act');
};

function menuChoise(event) {
  if (!event.target.matches('li')) return;
  const hrefText = event.target.textContent;
  const href = event.target;
  activHref(href);
  switch (hrefText) {
    case 'Categories':
      renderCategories();
      closeMenu(menuBtn);
      title.innerHTML = 'Categories';
      break;
    case 'Statistic':
      renderTable();
      closeMenu(menuBtn);
      title.innerHTML = 'Statistic';
      break;
    default:
      cardsOfCategory(event);
      closeMenu(menuBtn);
      break;
  }
  clearGame();
  renderGame();
}

export function renderMenu() {
  menuWrap.textContent = '';
  const menu = document.querySelector('.menu');
  menu.insertAdjacentHTML('afterBegin', '<li class = "active">Categories</li><li>Statistic</li>');
  categoryList.forEach((elem) => {
    menu.insertAdjacentHTML('afterBegin', `<li>${elem.title}</li>`);
    menu.addEventListener('click', menuChoise);
  });
}

export function openMenu() {
  menuBtn.addEventListener('click', function open(e) {
    e.preventDefault();
    if (!this.classList.contains('is-active')) {
      this.classList.add('is-active');
      menuWrap.classList.add('act');
    } else {
      closeMenu(this);
    }
  });
}

menuWrap.addEventListener('mouseleave', () => closeMenu(menuBtn));
