import { findWord, renderGame } from './game';
// eslint-disable-next-line import/no-cycle
import { renderMenu, openMenu } from './menu';
import { playSound } from './audio';
import { renderCategories } from './renderCategory';

const wrap = document.querySelector('.wrapper');
const element = document.querySelector('input[type=checkbox]');
function flipCard(event) {
  if (event.target.closest('.category-wrap')) return;
  if (!event.target.matches('button')) return;
  if (event.target.textContent === 'repeat word'
   || event.target.textContent === 'reset statitics') return;
  const card = event.target.parentNode.parentNode;
  card.classList.add('rotate');
  card.addEventListener('mouseleave', () => card.classList.remove('rotate'));
}

export default function init() {
  wrap.addEventListener('click', (event) => playSound(event));
  wrap.addEventListener('click', (event) => flipCard(event));
  wrap.addEventListener('click', (event) => findWord(event));
  element.addEventListener('change', renderGame);

  renderCategories();
  renderMenu();
  openMenu();
}
