import { cards } from './cards';
// eslint-disable-next-line import/no-cycle
import resetStorage from './script';
import { renderDifficultWord } from './difficultWord';

const wrap = document.querySelector('.wrapper');
export default function renderTable() {
  while (wrap.firstChild) {
    wrap.removeChild(wrap.firstChild);
  }
  const mainWrap = document.createElement('div');
  const tableWrap = document.createElement('div');
  const table = document.createElement('table');
  const tbody = document.createElement('tbody');

  tableWrap.classList.add('tableWrap');
  mainWrap.classList.add('mainWrap');

  wrap.append(mainWrap);
  mainWrap.append(tableWrap);
  mainWrap.insertAdjacentHTML('afterbegin',
    '<button class ="btnReset">reset statitics</button><button class ="btnPlay">repeat word</button>');
  tableWrap.append(table);
  table.append(tbody);
  tbody.insertAdjacentHTML('afterBegin', `<tr>
  <th>Category</th>
  <th>Word</th>
  <th>Translation</th>
  <th>Train</th>
  <th>Play</th>
  <th>Errors</th>
  <th>%</th>
  </tr>`);
  cards.forEach((elem) => {
    const key = elem.word;
    const localWord = JSON.parse((localStorage.getItem(key)));
    const percentErr = (localWord.guessWord !== 0)
      ? Math.floor((100 / (localWord.errorWord + localWord.guessWord)) * localWord.errorWord)
      : 0;
    tbody.insertAdjacentHTML('beforeend', `
    <tr>
    <td>${localWord.category}</td>
    <td>${localWord.word}</td>
    <td>${localWord.translation}</td>
    <td>${localWord.clickTrain}</td>
    <td>${localWord.guessWord}</td>
    <td>${localWord.errorWord}</td>
    <td>${percentErr}</td>
    </tr>`);
  });
  const reset = document.querySelector('.btnReset');
  reset.addEventListener('click', resetStorage);

  const play = document.querySelector('.btnPlay');
  play.addEventListener('click', (event) => renderDifficultWord(event));
}
