import isGameMode from './gameMode';

export function playGameSound(url) {
  const mp3 = new Audio(`./assets/${url}`);
  mp3.play();
}

export function playSound(event) {
  if (event.target.closest('.category-wrap')) return;
  if (!event.target.closest('.flip-card')) return;
  if (isGameMode()) return;
  if (event.target.matches('button')) return;

  const key = event.target.parentNode.parentNode.dataset.value;
  const localWord = JSON.parse((localStorage.getItem(key)));
  localWord.clickTrain += 1;
  localStorage.setItem(key, JSON.stringify(localWord));

  const card = event.target.parentNode.parentNode;
  const audio = card.querySelector('audio');
  audio.currentTime = 0;
  audio.play();
}
