import { cards } from './cards';
// eslint-disable-next-line import/no-cycle
import { renderGame } from './game';

const MAX_LENGTH = 8;

function getArray() {
  const wordListStorage = [];
  cards.forEach((elem) => {
    const key = elem.word;
    const wordList = JSON.parse((localStorage.getItem(key)));
    wordListStorage.push(wordList);
  });
  return wordListStorage;
}

export function sort() {
  const WordListArr = getArray();
  WordListArr.sort((a, b) => (a.errorWord < b.errorWord ? 1 : -1)).slice(0, MAX_LENGTH);
  const difficultWordList = WordListArr.slice(0, MAX_LENGTH).filter((el) => el.errorWord > 0);
  return difficultWordList.sort(() => Math.random() - 0.5);
}

const wrap = document.querySelector('.wrapper');
export function renderDifficultWord(event) {
  event.preventDefault();
  const page = sort();
  while (wrap.firstChild) {
    wrap.removeChild(wrap.firstChild);
  }
  if (page.length === 0) {
    wrap.insertAdjacentHTML('afterBegin', `<p class = "title">
    Вы пока что не допустили ошибок 
    </p>`);
  } else {
    page.forEach((elem) => {
      wrap.insertAdjacentHTML('afterBegin', `<div class="flip-card-container" data-categories="${elem.category}">
    <div class="flip-card" data-value=${elem.word}>
    <audio src="./assets/${elem.audioSrc}"></audio>
      <div class="card-front">
          <img class="card__img" src="./assets/${elem.image}">
        <p class = "card__title">${elem.word}</p>
        <button  class = "card__btn" >
        </button>
      </div>
      <div class="card-back">
        <div>
          <img class="card__img" src="./assets/${elem.image}">
        </div>
        <div>
          <span class = "card__title">${elem.translation}</span>
        </div>
      </div>
    </div>
  </div>`);
    });
    renderGame();
  }
}
