import { categoryList } from './cards';
// eslint-disable-next-line import/no-cycle
import { renderGame } from './game';
import getArr from './utils';

const wrap = document.querySelector('.wrapper');
const star = document.querySelector('.stars');
const titlePage = document.querySelector('.title');

export function cardsOfCategory(event) {
  let key;
  star.innerHTML = '';
  if (!event.target.closest('.category-wrap') && !(event.target.closest('.menu'))) return;
  if (event.target.closest('.category-wrap')) {
    key = event.target.closest('.category-wrap').textContent.trim();
  } else {
    key = event.target.textContent;
  }

  const li = document.querySelectorAll('li');
  li.forEach((x) => {
    x.classList.remove('active');
    if (x.textContent === key) {
      x.classList.add('active');
    }
  });

  const res = getArr(key);
  while (wrap.firstChild) {
    wrap.removeChild(wrap.firstChild);
  }

  titlePage.innerHTML = `${key}`;
  res.forEach((elem) => {
    wrap.insertAdjacentHTML('afterBegin', `<div class="flip-card-container" data-categories="${elem.category}">
    <div class="flip-card" data-value=${elem.word}>
    <audio src="./assets/${elem.audioSrc}"></audio>
      <div class="card-front">
          <img class="card__img" src="./assets/${elem.image}">
        <p class = "card__title">${elem.word}</p>
        <button  class = "card__btn" >
        </button>
      </div>
      <div class="card-back">
        <div>
          <img class="card__img" src="./assets/${elem.image}">
        </div>
        <div>
          <span class = "card__title">${elem.translation}</span>
        </div>
      </div>
    </div>
  </div>`);
  });
  renderGame();
}

export function renderCategories() {
  while (wrap.firstChild) {
    wrap.removeChild(wrap.firstChild);
  }
  categoryList.forEach((elem) => {
    const cardsWrap = document.createElement('div');
    const cardsImg = document.createElement('img');
    const cardTitle = document.createElement('p');

    cardsWrap.classList.add('category-wrap');
    cardsImg.classList.add('category-img');
    cardTitle.classList.add('category-title');

    wrap.append(cardsWrap);
    cardsWrap.append(cardTitle);
    cardsWrap.append(cardsImg);
    cardsImg.src = `./assets/${elem.image}`;
    cardTitle.innerHTML += elem.title;
    cardsWrap.addEventListener('click', cardsOfCategory);
  });
}
