import '../css/style.css';
import { cards } from './cards';
// eslint-disable-next-line import/no-cycle
import renderTable from './statistic';
// eslint-disable-next-line import/no-cycle
import init from './init';

function setStorage() {
  cards.forEach((element) => {
    const wordOfCard = element;
    if (JSON.parse(localStorage.getItem(`${wordOfCard.word}`) === null)) {
      wordOfCard.clickTrain = 0;
      wordOfCard.guessWord = 0;
      wordOfCard.errorWord = 0;
      localStorage.setItem(`${wordOfCard.word}`, JSON.stringify(wordOfCard));
    }
  });
}

export default function resetStorage() {
  cards.forEach((elem) => {
    const wordOfCard = elem;
    if (!JSON.parse(localStorage.getItem(`${wordOfCard.word}`) === null)) {
      wordOfCard.clickTrain = 0;
      wordOfCard.guessWord = 0;
      wordOfCard.errorWord = 0;
      localStorage.setItem(`${wordOfCard.word}`, JSON.stringify(wordOfCard));
    }
  });
  renderTable();
}

init();
setStorage();
