import getArr from './utils';
// eslint-disable-next-line import/no-cycle
import { renderCategories } from './renderCategory';
import { playGameSound, playSound } from './audio';
import isGameMode from './gameMode';
// eslint-disable-next-line import/no-cycle
import { sort } from './difficultWord';

const start = document.querySelector('.start');
const star = document.querySelector('.stars');
const wrap = document.querySelector('.wrapper');
const sound = {
  success: 'audio/success.mp3',
  error: 'audio/error.mp3',
  correct: 'audio/correct.mp3',
  fail: 'audio/failure.mp3',
};

let i = 0;
let errorsCount;
let currentWord;
let currentSound;
let currentCardsOfPlay = [];

function StartGame() {
  const pageTiile = document.querySelector('.title');
  const categ = document.querySelector('.flip-card-container');
  const key = categ.dataset.categories;
  if (pageTiile.innerHTML === 'Statistic') {
    currentCardsOfPlay = sort();
  } else {
    currentCardsOfPlay = getArr(key);
  }
  currentSound = currentCardsOfPlay[0].audioSrc;
  currentWord = currentCardsOfPlay[0].word;
  start.innerHTML = '';
  start.classList.add('repeat');
  start.removeEventListener('click', StartGame);
  playGameSound(currentSound);
}

export function clearGame() {
  const title = document.querySelectorAll('.card__title');
  const btnCard = document.querySelectorAll('.card__btn');
  const imgCard = document.querySelectorAll('.card__img');
  const categoryWrap = document.querySelectorAll('.category-wrap');
  const elemNon = [...title, ...btnCard, ...imgCard];
  const card = document.querySelectorAll('.flip-card');

  star.innerHTML = '';
  start.style.display = 'none';
  start.classList.remove('repeat');
  elemNon.forEach((elem) => {
    const elemHide = elem;
    if (!elemHide.classList.contains('card__img')) {
      elemHide.classList.remove('none');
    } else {
      elemHide.style.height = '200px';
    }
  });
  categoryWrap.forEach((elem) => {
    elem.classList.remove('active-bgr');
  });
  card.forEach((elem) => {
    elem.classList.remove('find-card');
  });
}

export function renderGame() {
  errorsCount = 0;
  const title = document.querySelectorAll('.card__title');
  const btnCard = document.querySelectorAll('.card__btn');
  const imgCard = document.querySelectorAll('.card__img');
  const categoryWrap = document.querySelectorAll('.category-wrap');

  const listHide = [...title, ...btnCard, ...imgCard];

  if (isGameMode()) {
    i = 0;
    start.addEventListener('click', StartGame);
    wrap.removeEventListener('click', (event) => playSound(event));
    if (!(document.querySelector('.flip-card-container') === null)) {
      start.style.display = 'block';
    }
    if (categoryWrap) {
      categoryWrap.forEach((elem) => {
        elem.classList.add('active-bgr');
      });
    }
    start.innerHTML = 'Start Game';
    listHide.forEach((elem) => {
      const elemHide = elem;
      if (!elemHide.classList.contains('card__img')) {
        elemHide.classList.add('none');
      } else {
        elemHide.style.height = '100%';
      }
    });
  } else {
    clearGame();
  }
}

function isWin() {
  if (i >= currentCardsOfPlay.length - 1) {
    const win = document.createElement('div');
    const text = document.createElement('p');
    document.body.append(win);
    win.append(text);
    if (errorsCount === 0) {
      win.classList.add('win');
      text.innerHTML = 'Вы выиграли';
      playGameSound(sound.success);
    } else if (errorsCount === 1) {
      win.classList.add('fail');
      text.innerHTML = `${errorsCount} error`;
      playGameSound(sound.fail);
    } else {
      win.classList.add('fail');
      text.innerHTML = `${errorsCount} errors`;
      playGameSound(sound.fail);
    }
    setTimeout(() => {
      clearGame();
      while (wrap.firstChild) {
        wrap.removeChild(wrap.firstChild);
      }
      win.remove();
      text.remove();
      renderCategories();
      renderGame();
      win.classList.remove('win');
      win.classList.remove('fail');
    }, 3000);
  }
}

function nextWord() {
  if (i >= currentCardsOfPlay.length - 1) return;
  const src = currentCardsOfPlay[i + 1].audioSrc;
  currentWord = currentCardsOfPlay[i + 1].word;
  playGameSound(src);
  i += 1;
}

start.onclick = () => {
  if (start.classList.contains('repeat')) {
    const srcAuduo = currentCardsOfPlay[i].audioSrc;
    playGameSound(srcAuduo);
  }
};

let errorsOnce = 0;
export function findWord(event) {
  if (!isGameMode()) return;
  const card = event.target.parentNode.parentNode;
  if (isGameMode() && start.classList.contains('repeat')
  && card.classList.contains('flip-card')
  && !card.classList.contains('find-card')) {
    const key = card.dataset.value;
    const localWord = JSON.parse((localStorage.getItem(key)));
    if (card.dataset.value === currentWord) {
      localWord.guessWord += 1 + errorsOnce;
      localWord.errorWord += errorsOnce;
      localStorage.setItem(key, JSON.stringify(localWord));
      errorsOnce = 0;
      card.classList.add('find-card');
      setTimeout(() => {
        isWin();
      }, 1000);
      playGameSound(sound.correct);
      star.insertAdjacentHTML('beforeEnd', '<div class="star correct"></div>');
      setTimeout(() => {
        nextWord();
      }, 1000);
    } else {
      star.insertAdjacentHTML('beforeEnd', '<div class="star error"></div>');
      playGameSound(sound.error);
      errorsCount += 1;
      errorsOnce += 1;
    }
  }
}
